import { store, getContext, useState, useEffect } from "@wordpress/interactivity";

interface Fruits {
  id: number,
  data: {
    apple: number
    orange: number
  }
}
interface Context {
  fruits: Fruits[]
}

const { state } = store('MyCounter', {
  state: {
    message: 'HellO World'
  },
  actions: {
    newSetOfFruits() {
      const { fruits } = getContext<Context>()
      fruits.push({ id: fruits.length, data: { apple: 2, orange: 4 } })
    },
    addOrange() {
      const context = getContext();
      // context.item.data.orange++
      // console.log(console.log('Orange added to fruit', context.item));
    }
  },

})